.login {
  width: 400px;
  margin: 0 auto;
  background: #fff;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, .1), 0 1px 1px rgba(0, 0, 0, .1);

  &-wrapper {
    //padding: 40px 0;
  }

  &__logo {
    display: block;
    padding: 40px 0 0 0;
    text-align: center;
  }

  &__social {
    display: inline-block;
    cursor: pointer;

    &-wrapper {
      padding-top: 10px;
      text-align: center;
    }

    &-icon {
      width: 50px;
      height: auto;
      margin: 10px;
    }

  }

  &__form {
    padding: 0 20px 20px 20px;

    &-title {
      text-align: center;
      display: block;
      font: 300 32px Roboto, sans-serif;
      color: #000;
    }

    &-box-or {
      text-align: center;
      position: relative;
      top: 2px;
      padding-top: 1px;
      margin-bottom: 14px;
    }

    &-or {

      &-wrapper {
        position: relative;
        padding-top: 10px;
        text-align: center;
      }

      &-hr {
        position: relative;
        border: 0;
        width: 100%;
        border-top: 1px solid #eaeaea;
      }

      &-title {
        position: relative;
        top: -20px;
        padding: 0 10px;
        width: 100%;
        font: 400 14px Roboto, sans-serif;
        color: #777;
        background: #fff;
      }

    }

    &-btn {
      display: block;
      width: 100%;
      padding: 7px 10px;
      margin-top: 25px;
      text-align: center;
      background: #f7f7f7;
      border-radius: 5px;
      border: 1px solid #d8d8d8;
      text-decoration: none;
      font: 400 16px Roboto, sans-serif;
      outline: 0;
      transition: color .2s, background .1s;

      &:hover {
        background-color: #f7f7f7;
      }

      &:active {
        background-color: #f1f1f1;
      }

    }

    &-pass {

      &-wrap {
        margin-top: 15px;
        text-align: center;
      }

      &-link {
        font: 400 14px Roboto, sans-serif;
        color: #777;

        &:hover {
          color: #0D47A1;
        }

      }

    }

  }

  &__new {
    display: block;
    border-top: solid 1px #eaeaea;
    padding: 20px;

    &-btn {
      display: block;
      width: 100%;
      padding: 20px 10px;
      text-align: center;
      background: #EF6C00;
      border-radius: 5px;
      text-decoration: none;
      font: 400 18px Roboto, sans-serif;
      color: #fff;
      outline: 0;
      transition: color .2s, background .1s;

      &:hover {
        background-color: #ff7200;
      }

      &:active {
        background-color: #EF6C00;
      }

    }

  }

}
